<template>
  <div class="ds-control ds-form-item ds-textbox mobile" :class="getCss">
    <van-field v-if="!readOnly" v-model="inputValue" :placeholder="placeholder" :type="inputType" :disabled="disabled" :label="showLabel&&label" :required="required" :input-align="inputAlign"></van-field>
    <van-field v-if="readOnly" v-model="inputValue" :placeholder="placeholder" :type="inputType" :disabled="disabled" :input-align="'left'"></van-field>
  </div>
</template>
<script>
import formControl from "_platform/dsf/mixins/formControl";
export default dsf.component({
  name: "DsfMobileTextbox",
  mixins: [formControl],
  ctrlCaption: "文本框",
  props: {
    ishideCon: {
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: "请输入"
    },
    metadata: {
      type: Object,
      default() {
        return dsf.metadata.get("text-meta-data");
      }
    },
    inputAlign: {
      type: String,
      default: 'right'
    }
  },
  computed: {
    inputType() {
      return this.ishideCon ? "password" : "text";
    },
    inputValue: {
      get() {
        return this.value;
      },
      set(v) {
        this.emitValueChange(v);
      }
    }
  },
  methods: {
  }
})
</script>