import {
  Toast,
  Dialog
} from 'vant';

export default {
  openDialog() {},
  message: function(options, type) {
    if (dsf.isUnDef(type)) {
      Toast(options);
    } else {
      let str = options.message;
      if (type !== false) {
        Toast.success(str)
      } else {
        Toast.fail(str);
      }
    }
  },
  alert: function(options) {
    if (dsf.type(options) == "string") {
      options = {
        message: options
      };
    }
    return Dialog.alert(options);
  },
  confirm: function(options) {
    if (dsf.type(options) == "string") {
      options = {
        message: options
      };
    }
    return Dialog.confirm(options);
  }
}