<template>
  <div class="ds-control ds-page mobile" :class="getCss">
    <!--页头-->
    <div v-if="hasHeader" class="ds-page-head">
      <div ref="head" class="head-inner" :slot-name="slots[1].name">
        <slot :name="slots[1].name"></slot>
      </div>
    </div>
    <!--页面主干-->
    <div class="ds-page-content" :style="{ width: isDesign ? 'auto' : pageWidth, margin: 'auto' }">
      <!--左侧视图-->
      <div v-show="leftViewShow" class="ds-page-west">
        <div
          ref="left"
          class="left-inner"
          :slot-name="slots[3].name"
          style="overflow: auto;"
          :style="{
            width: leftViewWidth,
            height: centerHeight + 'px',
            top: headHeight + 'px',
            bottom: footHeight + 'px',
          }"
        >
          <slot :name="slots[3].name"></slot>
        </div>
      </div>
      <!--中视图-->
      <div class="ds-page-center">
        <div
          ref="center"
          class="center-inner"
          :slot-name="slots[0].name"
          :style="{
            'margin-top': headHeight + 'px',
            'margin-bottom': footHeight + 'px',
            'margin-left': leftViewWidth,
            'margin-right': rightViewWidth,
          }"
        >
          <slot :name="slots[0].name">
          </slot>
        </div>
      </div>
      <!--右侧视图-->
      <div v-show="rightViewShow" class="ds-page-east" style="overflow: auto;">
        <div
          ref="right"
          class="right-inner"
          :slot-name="slots[4].name"
          style="overflow: auto;"
          :style="{
            width: rightViewWidth,
            height: centerHeight + 'px',
            top: headHeight + 'px',
            bottom: footHeight + 'px',
            'margin-right': '0px',
            'margin-left': '-' + rightViewWidth,
          }"
        >
          <slot :name="slots[4].name"></slot>
        </div>
      </div>
    </div>
    <div v-if="hasFooter" class="ds-page-foot">
      <div ref="foot" class="foot-inner" :slot-name="slots[2].name">
        <slot :name="slots[2].name"></slot>
      </div>
    </div>
  </div>
</template>
<script>
import view from "_platform/dsf/mixins/view";
export default dsf.component({
  name: "DsfMobilePage",
  ctrlCaption: "移动端页面",
  mixins: [view],
  props: {
    slots: {
      type: Array,
      default() {
        return [
          { name: "center", controls: [] },
          { name: "head", controls: [] },
          { name: "foot", controls: [] },
          { name: "left", controls: [] },
          { name: "right", controls: [] }
        ];
      }
    }
  },
  data() {
    return {
      headHeight: 0,
      footHeight: 0,
      centerHeight: "auto"
    };
  },
  computed: {
    leftViewWidth() {
      let views = this.viewSplit.split(",");
      return views[0];
    },
    rightViewWidth() {
      let views = this.viewSplit.split(",");
      return views[1];
    },
    leftViewShow() {
      let w = parseFloat(this.leftViewWidth);
      return w != 0;
    },
    rightViewShow() {
      let w = parseFloat(this.rightViewWidth);
      return w != 0;
    },
    getCss(){
      return [
        ...this.$getClass(),
        this.topBottom ? "top-bottom-" + this.topBottom : "",
        this.leftRight ? "left-right-" + this.leftRight : ""
      ]
    }
  },
  created() { },
  destroyed() {
  },
  mounted() {
    // window.page = this;
    console.timeEnd("加载页面");
    if (this.isDesign) {
      this.watchCenterHeight();
    } else {
      this.resize();
    }
  },
  methods: {
    watchCenterHeight() {
      this.setCenterHeight();
      window.setTimeout(this.watchCenterHeight, 1000);
    },
    setCenterHeight() {
      let head = this.$refs.head ? this.$refs.head : null;
      let foot = this.$refs.foot ? this.$refs.foot : null;
      this.headHeight = head ? $(head).outerHeight() : 0;
      this.footHeight = foot ? $(foot).outerHeight() : 0;
    },
    resize() {
      this.setCenterHeight();
    }
  }
});
</script>