import NProgress from 'nprogress';
import 'nprogress/nprogress.css';

import "_platform/dsf/utils";
import "../dsf_config";

//注入平台系统元数据
import "_platform/dsf/metadata";
import "../metadata";
//注入平台基础组件
import bhc from "_platform/dsf/mobile/bhc";
//注入平台全局过滤器
import filters from "_platform/dsf/filters";
//用于加载配置完成的页面,预览页面
import output from '_platform/dsf/output'
import api from '../api';


//此处引入要使用的组件
import App from "./App.vue";
import store from "../store";
import router from "./router";
import Vue from "vue";

NProgress.configure({
  easing: 'ease-in',
  minimum: 0.1,
  showSpinner: false
});

Vue.use(bhc);
Vue.use(filters);
Vue.use(output);
Vue.prototype.dsf = dsf;
Vue.prototype.$http = dsf.http;
Vue.config.productionTip = false;

//注入WEBAPI
Object.defineProperties(Vue.prototype, {
  $webAPI: {
    get: function() {
      if (!this.$$webapi) {
        this.$$webapi = {};
        for (let k in api) {
          this.$$webapi[k] = api[k].bind(this)
        }
      }
      return this.$$webapi;
    }
  }
});
new Vue({
  store,
  router,
  created() {
    //移动端页面增加rem适配
    dsf.flexble();
  },
  render: h => h(App)
}).$mount("#app");